<template>
  <div
    id="delProductModal"
    ref="delProductModal"
    class="modal fade"
    tabindex="-1"
    aria-labelledby="delProductModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-dialog-centered">
      <div class="modal-content border-0">
        <div class="modal-header bg-danger text-white">
          <h5 id="delProductModalLabel" class="modal-title">
            <span>刪除產品</span>
          </h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          是否刪除
          <strong class="text-danger">{{ product.title }}</strong>
          商品(刪除後將無法恢復)。
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-outline-secondary"
            data-bs-dismiss="modal"
          >
            取消
          </button>
          <button
            type="button"
            class="btn btn-danger"
            @click="removeProduct(product.id)"
          >
            確認刪除
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import emitter from '@/utils/emitter.js'
import Modal from 'bootstrap/js/dist/modal'

export default {
  data () {
    return {
      delProductModal: '',
      product: []
    }
  },
  methods: {
    removeProduct (id) {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/product/${id}`
      this.$http
        .delete(api)
        .then((res) => {
          emitter.emit('get_product')
          this.delProductModal.hide()
          alert(res.data.message)
        })
        .catch((err) => {
          alert(err.data.message)
        })
    }
  },
  mounted () {
    this.delProductModal = new Modal(document.getElementById('delProductModal'))
    emitter.on('emitter', (product) => {
      this.product = product
      this.delProductModal.show()
    })
  }
}
</script>
